import * as React from 'react';
import './nav.css';
import Portfolio from '../Portfolio/Portfolio';
import Motivation from '../Motivation/Motivation';
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";


export default class Nav extends React.Component{

  render() {
    return (
            <div  className="nav">
              <div className="nav--inner">
                {/*<Link to="/" className={"nav--link " + ((this.props.active === 'home') ? "nav--active" : "" )}>Home</Link>*/}
                <Link to="/Portfolio" className={"nav--link " + ((this.props.active === 'portfolio') ? "nav--active" : "" )}>Portfolio</Link>
                <Link to="/Motivation" className={"nav--link " + ((this.props.active === 'motivation') ? "nav--active" : "" )}>Motivation</Link>
              </div>
            </div>
      );
  }
};


